@charset "utf-8";

.loading-screen{
  height: 100vh;
  overflow: hidden;
  position: relative;
  background-color:white;
  z-index: 1000;
}

.Loading{
  height: 100vh;
  z-index: 1000;
  position: relative;
  background-color:white;
  overflow: hidden;
}
  .loading_title {
    font-size: 70px;
    font-family: 'GmarketSansMedium', sans-serif;
    position: absolute;
    top: 60%;
    left: 20%;
    transform: translateY(-50%);
    display: flex;
    text-shadow: 1px 1px 0 white, -1px -1px 0 white, 1px -1px 0 white, -1px 1px 0 white;
  }
  
  .loading_title span{
    width: fit-content;
    display: inline-block;
    animation: wave 2s infinite;
    color: var(--txtColor);
  }
  
  .loading_title span:nth-child(1) { animation-delay: 0s;}
  .loading_title span:nth-child(2) { animation-delay: 0.1s; }
  .loading_title span:nth-child(3) { animation-delay: 0.2s; }
  .loading_title span:nth-child(4) { animation-delay: 0.3s; }
  .loading_title span:nth-child(5) { animation-delay: 0.4s; }
  .loading_title span:nth-child(6) { animation-delay: 0.5s; }
  .loading_title span:nth-child(7) { animation-delay: 0.6s; }
  .loading_title span:nth-child(8) { animation-delay: 0.7s; }
  .loading_title span:nth-child(9) { animation-delay: 0.8s; }
  .loading_title span:nth-child(10) { animation-delay: 0.9s; }
  .loading_title span:nth-child(11) { animation-delay: 1s; }
  .loading_title span:nth-child(12) { animation-delay: 1.1s; }
  .loading_title span:nth-child(13) { animation-delay: 1.2s; }
  .loading_title span:nth-child(14) { animation-delay: 1.3s; }
  .loading_title span:nth-child(15) { animation-delay: 1.4s; }
  .loading_title span:nth-child(16) { animation-delay: 1.5s; }
  .loading_title span:nth-child(17) { animation-delay: 1.6s; }
  .loading_title span:nth-child(18) { animation-delay: 1.7s; }
  .loading_title span:nth-child(19) { animation-delay: 1.8s; }
  .loading_title span:nth-child(20) { animation-delay: 1.9s; }
  .loading_title span:nth-child(21) { animation-delay: 2s; }

  .head_title{
    font-size: 30px;
    font-weight: bold;
    color: var(--mainColor);
    text-shadow: 
    1px 1px 0 var(--subColor),  
   -1px -1px 0 var(--subColor),  
    1px -1px 0 var(--subColor),  
   -1px 1px 0 var(--subColor); 
    position: absolute;
    top: 206px;
    width: 100%;
    text-align: center;
  }
  .head_sub_title{
    font-size: 60px;
    font-weight: bold;
    color: var(--mainColor);
    position: absolute;
    top: 315px;
    left: 45%;
  }
  
  .title_circle{
    margin: 0 auto;
    width: 90vmin; /* 화면의 %에 해당하는 최소값 */
    height: 90vmin; /* 화면의 %에 해당하는 최소값 */
    border-radius: 50%;
    background: linear-gradient(90deg, #4D74D8 1%, rgba(230,236,250,1) 99%);
    animation:ani01 4s linear infinite;
    margin: 5vmin auto;
  }
  .line{
    background-color: var(--mainColor);
    width: 60%;
    height: 5px;
    margin: 0 auto;
    position: absolute;
    bottom: 33%;
    left: 50%;
    transform: translateX(-50%);
  }
  
  .book{
    /* width: 720px; */
    width: 40%;
    height: 750px;
    background-color: var(--mainColor) !important;
    box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
    margin: 0 auto;
    margin-top: 70px;
    border-radius: 5px;
    position: relative;
    overflow: hidden;
    z-index: 1;
  }
  
  .circle{
    width: 600px;
    height: 600px;
    top: 183px;
    right: -100px;
  }
  .small_circle{
    width: 110px;
    height: 110px;
    bottom: 20px;
    left: 120px;
  }
  
  .circle, 
  .small_circle{
    border-radius: 50%;
    background-color: var(--txtColor2);
    position: absolute;
  }
  
  h3{
    text-indent: -9999px;
    font-size: 0px;
  }
  
  .book_head{
    width: 100%;
  }
  
  .book .book_text{
    font-size:100px;
    color: var(--txtColor2);
    font-weight: bolder;
    padding: 10px;
  }
  .text_bottom{
    width: 600px;
    text-align: right;
    position: absolute;
    bottom: 50px;
    right: 10px;
    color: var(--mainColor) !important;
  }
  .book .book_desc{
    position: absolute;
    top: 312px;
    right: 8px;
    color: var(--mainColor);
    font-weight: 500;
  }
  
  .book_desc p:nth-child(1){
    font-size: 16px;
    margin: 10px;
  }
  .book_desc p:nth-child(2){
    font-size: 60px;
    text-align: right;
    margin: 10px;
  }
  
  .modal-container{
    position: relative;;
  }
  .msgbox {
    border: 1px solid var(--subColor);
    border-radius: 30px;
    width: 150px;
    height: 70px;
    position: absolute;
    top: 50%;
    right: 17%;
    transform: translateY(-50%) translateX(-50%);
    animation: jump 1s infinite;
  }
  
  .msgbox {
    font-size: 20px;
    color: var(--mainColor);
    display: flex; /* Flexbox를 사용하여 아이템 정렬 */
    align-items: center; /* 수직 정렬을 가운데로 설정 */
    justify-content: center; /* 수평 정렬을 가운데로 설정 */
    padding: 15px;
    text-decoration: none; /* 링크의 기본 밑줄 제거 */
  }

  
  .msgbox img {
    height: 40px; /* 이미지의 높이 설정 */
    width: auto; /* 비율 유지 */
    margin-right: 10px; /* 이미지와 텍스트 사이의 간격 설정 */
  }
  
  .msgbox > span {
    font-size: var(--fontTitle3);
    display: inline-flex; /* Span의 높이 맞추기 */
    align-items: center; /* 수직 정렬을 가운데로 설정 */
    height: 40px; /* 높이 설정 (이미지와 맞춤) */
    padding: 0 10px; /* 좌우 패딩 조정 */
    border-radius: 20px; /* 배경색의 둥근 모서리 설정 */
  }

  
    