@charset "utf-8";

/* 로딩화면 땡큐폴커밍 글자 */
@keyframes wave {
    0%, 100% {
        transform: translateY(0);
        color: var(--txtColor);
    }
    50% {
        transform: translateY(-20px);
        color: var(--mainColor);
    }
}

/* 대문 무한으로 돌아가는 원 */
@keyframes ani01{100%{transform: rotate(-360deg);}}

/* 흐르는 텍스트 */
.line_text,
.line_text2,
.line_text3
{
  font-size: 60px;
  color: var(--subColor);
  font-weight: 500;
  position: absolute;
  height: 60px;
  width: 7000px;
  overflow: hidden;
}
.line_text{
  top: -50px;
  right: 0px;
  animation: marquee 40s linear infinite;
}
.line_text2{
  top: -100px;
  right: 0px;
  animation: marquee2 40s linear infinite;
}
.line_text3{
  text-align: right;
  bottom: -200px;
  right: -100px;
  animation: marquee3 30s linear infinite;
}

/* 대문 - 글자 무한 루프 */
@keyframes marquee {
    from { 
        transform: 
        rotate(5deg) 
        translateX(50%)
        ;
    }
    to { 
        transform: 
        rotate(5deg) 
        ;
    }
    }
@keyframes marquee2 {
    from { 
        transform: 
        rotate(15deg) 
        translateX(50%)
        ;
    }
    to { 
        transform: 
        rotate(15deg) 
        ;
    }
}
@keyframes marquee3 {
    from { 
        transform: 
        rotate(-15deg) 
        ;
    }
    to { 
        transform: 
        rotate(-15deg) 
        translateX(50%)
        ;
    }
}

/* 대문 click */
@keyframes jump {
    0%{
        transform: translateX(0px);
    }
    50%{
        transform: translateX(10px);
    }
    100%{
        transform: translateX(0px);
    }
}

/* 나의 스킬 버튼  */
@keyframes btn_loop {
    to { transform:translateY(-5px) scale(1.1); }
}


/* 함께 일하는 즐거움 */
@keyframes show_text {
    0%{
        scale: 0.8;
    }
    50%{
        font-size: var(--fontStrong);
    }    
    100%{
        font-size: var(--fontStrong);
    }    
}


/* 나를 소개합니다 */
@keyframes show {
0% {
    opacity: 0; /* 애니메이션 시작 시 투명 */
    transform: scale(0.8); 
}
50% {
    opacity: 1; /* 애니메이션 중간 시 완전히 보임 */
    transform: scale(1); 
}
100% {
    opacity: 1; /* 애니메이션 끝 시에도 계속 보임 */
    transform: scale(1); 
}
}


/* 걸어다니는 연필 - 목차 */
@keyframes walkingX {
    0%{left: 0;top: 60%;}
    50%{left: 50%; top: 20%;}
    100%{left: 90%; top: 50%;}
}
@keyframes walkingY {
    0%{transform: translateY(-30px);}
    100%{transform: translateY(30px);}
}
@keyframes walkingX2 {
    0%{right: 0;bottom: 10%;}
    50%{right: 50%; bottom: 10%;}
    100%{right: 90%; bottom: 30%;}
}
@keyframes walkingY2 {
    0%{transform: translateY(-50px);}
    100%{transform: translateY(50px);}
}