@charset "utf-8";
  
/* 모달 배경 */
.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0,0,0); 
  background-color: rgba(0,0,0,0.6); 
  z-index: 10;
}

/* 모달 내용 */
.modal-content {
  scroll-behavior: smooth;
  border-radius: 10px;
  background-color: var(--txtColor2);
  margin: 20px auto;
  border: 1px solid #888;
  width: 95%;
  height: 95%;
  overflow: auto;
}

.modal-content::-webkit-scrollbar {
  display: none; /* 스크롤바 숨김 */
}

.btn{
  background-color: var(--mainColor);
  text-align: center;
  /* line-height: 20px; */
  border-radius: 5px;
  color:var(--txtColor2);
  border: 2px solid var(--mainColor);
  display: inline-block;
  width: 130px;
  line-height: 30px;
}

.btn:hover{
  background-color: var(--txtColor2);
  color: var(--mainColor);
  border: 2px solid var(--mainColor);
  font-weight: 500;
}



/* 닫기 버튼 */
.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}


  .modal_header{
    background-color: white;
    border-bottom: 1px solid var(--subColor);
    padding: 20px;
    height: 100px;
    position: sticky; top: 0px;left: 0px;
  }
  .modal_main {
    margin: 20px auto;
    width: 80%;
  }

  .login{
    position: absolute;
    top: 104px;
    right: 23px;
    font-size: var(--fontsmall);
  }
  .desc{
    height: fit-content;
    display: flex;
  }

  .mockup{
    height: 370px;
  }

  .intro{
    /* padding: 0px 10px 10px 30px; */
    padding: 30px;
  }
  .intro p{
    font-size: var(--fontTitle3);
    font-weight: 500;
    padding: 10px 0;
    color: var(--mainColor);
  }
  .intro p i{
    padding-right: 10px;
  }

  .intro ul:nth-of-type(1){
    margin-bottom: 20px;
  }

  .intro ul {
    margin-left: 40px;
  }
  .intro ul li{
    line-height: 30px;
  }

  .modal article > ul{
    margin: 10px 0;
    display: flex;
    justify-content: space-around;
  }
  .modal article > ul >li{
    width: 50%;
    margin-top: 50px;
  }

  .modal h2{
    width: 60px;
    height: 60px;
    position: absolute;
    top: 20px;
    left: 18px;
    text-indent: 0px;
  }
  .modal h2 img {
    width: 100%;
    height: 100%;
  }
  .modal h3{
    text-indent: 0px;
    color: var(--mainColor);
    font-size: var(--fontTitle3) !important;
    font-weight: bold;
    position: absolute; top: 20px; left: 90px; 
  }
  .modal article{
    width: 100%;
    height: fit-content;
  }
  .title_list{
    display: flex;
    position: absolute;top: 55px;left: 85px;
  }
  .title_list li{
    list-style: none;
    border: 1px solid var(--mainColor);
    border-radius: 30px;
    padding: 5px 15px;
    margin: 0 5px;
    color: var(--mainColor);
    font-size: var(--fontsmall);
    transition: 0.3s;
  }

  .close{
    color: var(--mainColor);
    border-radius: 10px;
    padding: 9px 12px;
    border: 1px solid var(--subColor);
    transition: 0.3s;
    position: absolute; top: 25px; right: 20px;
  }
  .title_list li:hover,
  .close:hover,
  .site_view{
    background-color: var(--mainColor);
    color: var(--txtColor2);
  }
  .site_view{
    padding: 15px 17px;
    border-radius: 10px;
    border: 1px solid var(--mainColor);
    position: absolute; top: 25px; right: 80px;
    transition: 0.3s;
  }
  .site_view:hover {
    background-color: var(--txtColor2);
    color: var(--mainColor);
    font-weight: bold;
  }

  .view2{
    display: none;
  }

  .page_detail{
    width: 100%;
    padding: 50px 0;
  }
  .page_detail > ul{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    border-top: 1px solid var(--subColor);
    padding: 30px;
  }
  .page_detail > ul >li{
    height: fit-content;
    min-width: 500px;
    width: 50%;
    padding: 20px 0;
    text-align: center;
  }
  .page_detail > ul >li img{
    width: 70%;
  }
  .mobile_img{
    width: 40% !important;
  }
  .page_detail > ul >li> ul{
    font-size: var(fontBase);
    line-height: 25px;
  }

  .t_btn{
    padding: 10px 15px;
    background-color: var(--mainColor);
    color: var(--txtColor2);
    border-radius: 10px;
    position: absolute;
    bottom: 35px;
    right: 50px;
  }

  @media screen and (max-width: 1025px){
    *{
      font-size: 0.95em;
    }
    #page-title{
      font-size: 1.2rem !important;
      width: calc(100% - 200px);
      text-overflow:ellipsis;
      white-space: nowrap;
      overflow: hidden; 
    }
    .desc{
      display: flex;
      flex-direction: column;
    }
    .video_box{
      text-align: center;
    }
    .video{
      width: 100%;
    }

    article > ul{
      margin: 10px 0;
      display: flex;
      flex-direction: column;
    }
    article > ul >li{
      width: 100% !important;
    }

    .close{
      padding: 7px 10px;
      top: 15px;
      font-size: 20px;
    }

    .site_view{
      padding: 10px 12px;
      top: 15px; right: 65px;
    }

    .title_list{
      width: calc(100% - 100px);
    }
    .title_list li{
      font-size: 10px;
      text-overflow:ellipsis;
      white-space: nowrap;
      overflow: hidden; 
    }
    .view1{
      display: none;
    }
    .view2{
      display:block;
    }
  }

