@charset "utf-8";

nav{
  border-top: 2px solid #0038c8;
  /* border-bottom: 2px solid #0038c8; */
}

nav > ul{
  background-color: white !important;
  display: flex;
  justify-content: space-between;
  margin: 0;
  line-height: 40px;
  text-align: center;
}

nav > ul > li{
  width: 20%;
}

nav > ul > li >  ul{
  display: none;
  text-align: center;
}

nav > ul  li:hover ul{
  display: block;
}

nav > ul > li > a:hover{
  color: #0038c8;
}

nav > ul > li >  ul > li a:hover{
  color: #0038c8;
}

a{
  text-decoration: none;
  color: #222;
}