@charset "utf-8";

@font-face {
  font-family: 'GmarketSansMedium';
  src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

*{margin: 0; padding: 0;}

h2 {
  width: 160px;
  height: 160px;
  overflow: hidden; /* 자식 요소가 넘치지 않도록 */
  position: absolute;
  top: 292px;
  left: 38%;
  transform: translateX(-50%);
}

h2 a img {
  width: 100%;
  height: 100%;
}

header{
  width: 100%;
  height: 100px;
  position: fixed;
  top: 0px;
  background-color: white;
  z-index: 2;
}
header h1{
  width: 60px;
  height: 60px;
  margin: 0 auto;
  padding: 10px;
}

header h1 img{
  width: 100%;
}

section h2{
  text-align: center;
}


footer{
  background-color: #222;
  width: 100%;
  line-height: 40px;
  color: white;
  text-align: center;
  font-style: normal;
  position: fixed;
  bottom: 0px;
}

:root{
  --fontTitle : 2.5rem; /*40*/
  --fontTitle2 : 1.875rem; /*30*/
  --fontTitle3 : 1.5rem; /*24*/
  --fontMore : 1.125rem; /*18*/
  --fontStrong : 1rem; /*16px*/
  --fontBase : 0.875rem; /*14*/
  --fontsmall : 0.75rem; /*12*/

  --mainColor : #0038C8;
  --subColor : #E6ECFA;
  --txtColor : #222222;
  --txtColor2 : #ffffff;
}

* {
  box-sizing: border-box;
}

a{
  text-decoration: none;
  color: var(--txtColor);
}

li{
  list-style: none;
}
html, body{
  scroll-behavior: smooth;
  overflow-x: hidden; /* 수평 스크롤 방지 */
  font-family: 'GmarketSansMedium';
  /* height: 100vh; */
}

header{
  background-color: white;
}


.main_con{
  z-index: 1000;
}

.hidden{
  text-indent: -99999px;
  font-size: 0px;
}

html::-webkit-scrollbar {
  display: none; /* 스크롤바 숨김 */
}

input[type="checkbox"]{
  display: none;
}

/* 내용 */
li{
  list-style: none;
}

/* 나를 소개합니다 */
.intro_title{
  font-size: var(--fontTitle3);
  text-align: center;
}
.introduce{
  text-align: center;
}

.introduce p:nth-of-type(1){
  font-size: var(--fontTitle);
  margin: 30px 0 20px 0;
}
.introduce p:nth-of-type(2){
  font-size: var(--fontMore);
  font-weight: 500;
  margin: 10px 0 20px 0;
}
.introduce img:nth-of-type(2){
  width: 268px;
  height: 390px;
  border-radius: 10px;
}
.introduce p:nth-of-type(3){
  font-size: var(--fontStrong);
  padding: 20px;
  line-height: 25px;
  font-weight: 400;
}

.introduce_desc{
  animation: show 1s linear infinite;
  color:  var(--mainColor);;
}

/* 프로필 성장/경험 */
.profile {
  /* padding: 30px 20px 0px 100px; */
  text-align: center;
  padding: 30px;
}

.profile dl{
  line-height: 25px;
  margin: 0 0 20px 0;
}
.profile dl:nth-child(3){
  margin: 0px;
}
.profile dl dt,
.tech_skill .tech_title{
  color: var(--mainColor);
  font-size: var(--fontTitle3);
  font-weight: 600;
  line-height: 40px;
}

.profile dl dd span{
  background-color: var(--subColor);
  line-height: 27px;
}

.profile dl dd i{
  padding: 0 10px 0 0;
}
.profile dl dd:hover{
  color: var(--mainColor);
}
.fa-graduation-cap{
  padding: 0 4px 0 0 !important;
}

.profile dl dd{
  font-size: var(--fontBase);
}

/* 스킬 */
.tech_skill{
  max-width: 500px;
  width:90%;
  margin: 0 auto;
}

.tech_skill p{
  font-size: var(--fontMore);
  font-weight: 500;
  width: 150px;
}
.tech_skill div{
  display: flex;
  line-height: 60px;
}
/* .tech_skill ul li{
  text-indent: -9999px;
} */

.tech_skill ul li img{
  width: 250px;
}

/* 스킬들 */
.design,.frontend,.database,.etc{
  display: flex;
  justify-content: space-between;
}

.design li,.frontend li,.database li,.etc li{
  width: 40px;
  height: 40px;
  transition: 0.3s;
  animation: btn_loop 0.8s linear infinite;
  margin: 10px 5px;
  overflow: hidden;
}

.frontend li:nth-child(2) img{
  transform: translate(-54px, 0px);
}
.frontend li:nth-child(3) img{
  transform: translate(-105px, 0px);
}
.frontend li:nth-child(4) img{
  transform: translate(-211px, 0px);
}
.frontend li:nth-child(5) img{
  transform: translate(-105px, -46px);
}
.frontend li:nth-child(6) img{
  transform: translate(-156px, 0px);
}
.frontend li:nth-child(7) img{
  transform: translate(-53px, -46px);
}

.design li:nth-child(1) img{
  transform: translate(-104px, -138px);
}
.design li:nth-child(2) img{
  transform: translate(-211px, -138px);
}
.design li:nth-child(3) img{
  transform: translate(-159px, -138px);
}

.database li:nth-child(1) img{
  transform: translate(0px, -46px);
}
.database li:nth-child(2) img{
  transform: translate(-53px, -46px);
}
.database li:nth-child(3) img{
  transform: translate(-105px, -92px);
}
.database li:nth-child(4) img{
  transform: translate(-156px, -92px);
}
.database li:nth-child(5) img{
  transform: translate(-210px, -92px);
}

.etc li:nth-child(1) img{
  transform: translate(0px, -92px);
}
.etc li:nth-child(2) img{
  transform: translate(-52px, -92px);
}
.etc li:nth-child(3) img{
  transform: translate(-52px, -138px);
}

.project_box,
.contact_box,
.aboutme
{
  margin: 150px 10px 100px 10px;
}

.tag1, .tag2{
  color: var(--txtColor2);
  padding: 6px 10px;
  font-size: var(--fontsmall);
  border-radius: 5px;
  transform: translateY(10px);
}
.tag1{
  background-color: #212529;
  color: var(--txtColor2);
}
.tag2{
  background-color: #DC3545;
  color: var(--txtColor2);
}

.small{
  font-size: var(--fontBase);
  color: var(--mainColor);
}

.box_title{
  font-size: var(--fontTitle3);
  font-weight: 500;
  margin-bottom: 30px;
  text-align: center;
}

.project_img{
  width: 100%;
}
.project_img video,
.project_img img{
  width: 100%;
  padding: 10px 0;
  height: 350px; 
}

.project_desc table{
  text-align: left;
  width: 100%;
  margin-bottom: 10px; 
}

.project_desc caption{
  font-size: 0px;
}
.project_desc table{
  margin: 0 auto;
  width: 98%;
  max-width: 1000px;
  font-size: var(--fontBase);
  margin-bottom: 20px;
}

.project_desc table tr{
  line-height: 30px;
}
.project_desc table tr th{
  width: 20%;
  text-align: center;
}
.project_desc table tr td{
  width: 80%;
}

.desc_btn{
  text-align: center;
}

.desc_btn a{
  margin: 5px;
}



.contact_box{
  display: flex;
  width: 70vw;
  height: 40vh;
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.important{
  font-size: var(--fontMore);
  font-weight: 500;
}
.contact_box div{
  animation: show_text 3s infinite;
  background-color: var(--mainColor);
  margin: 5px;
  width: 33%;
  color: var(--txtColor2);
  text-align: center;
}
.contact_box div p{
  padding: 0 20px;
}
.contact_box div p:nth-child(1){
  padding: 65px 20px 50px;
  font-size: var(--fontMore);
  font-weight: 500;
}
.contact_box div p:nth-child(2){
  line-height: 25px;
}
.contact_box div p:nth-child(3){
  padding: 20px;
}
.contact_box div:nth-child(1),
.contact_box div:nth-child(3){
  border-radius: 150px 0 150px 0;
}
.contact_box div:nth-child(2){
  border-radius: 0 150px 0 150px;
}
.contact_box div p i{
  font-size: 40px;
  padding: 0 0 10px 0;
}

@media screen and (min-width:390px) and (max-width:768px) {

  .contact_box{
    height: 56vh !important;
    overflow-wrap: break-word;
    width: 90vw !important;
  }
  
}

@media screen and (min-width:320px) and (max-width:390px) {
  .contact_box{
    height: 56vh !important;
    overflow-wrap: break-word;
    width: 90vw !important;
  }

}

main{
  height: 100vh;
  overflow: hidden;
}